html {
  font-size: 16px;
}
@media screen and (max-width: 767px) {
  html {
    font-size: 14.4px;
  }
}

body {
  overflow-x: hidden;
  background-color: #ffffff;
  color: #333333;
  font-family: "Noto Sans JP", sans-serif;
  line-height: 1.5;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

.wrapper {
  overflow-x: hidden;
}

.ly-inner {
  width: 1100px;
  margin: 0 auto;
  max-width: 100%;
}
@media screen and (max-width: 1099px) {
  .ly-inner {
    padding: 0 40px;
  }
}
@media screen and (max-width: 991px) {
  .ly-inner {
    padding: 0 32px;
  }
}
@media screen and (max-width: 767px) {
  .ly-inner {
    padding: 0 16px;
  }
}

.ly-sm__container {
  width: 800px;
  margin: 0 auto;
  max-width: 100%;
  overflow-x: hidden;
}

.mod-header__main {
  background-color: inherit;
  color: #333333;
  position: relative;
  z-index: 1;
  padding: 1.5rem 0;
}
.mod-header__main .mod-header__inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.mod-header__main .mod-header__logo a {
  display: block;
  font-size: 2rem;
  font-weight: 500;
  font-family: "Spectral SC", serif;
  line-height: 40px;
  transition: all 0.3s ease 0s;
  z-index: 2;
}
.mod-header__main .mod-header__description {
  font-size: 1rem;
  margin-left: 16px;
}

.mod-header-nav {
  margin-left: auto;
}
.mod-header-nav .mod-header-nav__list {
  display: flex;
  justify-content: flex-start;
  flex-flow: nowrap row;
  align-items: center;
}
.mod-header-nav .mod-header-nav__list .navItem {
  padding: 8px 0;
  display: inline-block;
}
.mod-header-nav .mod-header-nav__list .navItem a {
  padding: 0 16px;
  transition: all 0.3s ease 0s;
  display: block;
}
.mod-header-nav .mod-header-nav__list .navItem + .navItem {
  margin-left: 8px;
}
@media screen and (max-width: 991px) {
  .mod-header-nav {
    display: none;
  }
}

.mod-drawer__container .mod-drawer__button {
  cursor: pointer;
  display: inline-block;
  height: 56px;
  overflow: hidden;
  padding: 4px;
  position: fixed;
  right: 4vw;
  top: 14px;
  width: 56px;
  z-index: 8;
}
@media screen and (max-width: 991px) {
  .mod-drawer__container .mod-drawer__button {
    top: 16px;
  }
}
@media screen and (max-width: 767px) {
  .mod-drawer__container .mod-drawer__button {
    top: 16px;
  }
}
@media screen and (max-width: 576px) {
  .mod-drawer__container .mod-drawer__button {
    right: 5px;
    top: 16px;
  }
}
.mod-drawer__container .mod-drawer__button::after {
  content: "";
  display: block;
  position: relative;
}
.mod-drawer__container .mod-drawer__button .button-line {
  background-color: #333333;
  height: 2px;
  left: 24%;
  margin: 0 auto;
  position: absolute;
  transition: all 0.3s ease-out 0s;
  user-select: none;
  width: 32px;
}
.mod-drawer__container .mod-drawer__button .button-line--1 {
  top: 17px;
}
.mod-drawer__container .mod-drawer__button .button-line--2 {
  top: 27px;
}
.mod-drawer__container .mod-drawer__button .button-line--3 {
  top: 37px;
}
.mod-drawer__container .mod-drawer__button[aria-expanded=true] .button-line {
  background-color: #333333;
}
.mod-drawer__container .mod-drawer__button[aria-expanded=true] .button-line--1 {
  top: 27px;
  transform: rotate(45deg);
}
.mod-drawer__container .mod-drawer__button[aria-expanded=true] .button-line--2 {
  left: 50%;
  width: 0;
}
.mod-drawer__container .mod-drawer__button[aria-expanded=true] .button-line--3 {
  top: 27px;
  transform: rotate(-45deg);
}
.mod-drawer__container .mod-drawer__overlay {
  background-color: #000;
  cursor: pointer;
  height: 100vh;
  left: 0;
  opacity: 0;
  position: fixed;
  top: 0;
  transition: all 0.3s ease 0s;
  visibility: hidden;
  width: 100%;
  z-index: 6;
}
.mod-drawer__container .mod-drawer__overlay[data-focus-visible=true] {
  opacity: 0.8;
  visibility: visible;
}
.mod-drawer__container .mod-drawer-nav .mod-drawer-nav__list {
  background-color: #fff;
  font-family: "Noto Sans JP", serif;
  font-size: 18px;
  font-weight: 400;
  height: 100vh;
  overflow-y: auto;
  padding-top: 52px;
  position: fixed;
  right: -320px;
  top: 0;
  transition: all 0.3s ease-out 0s;
  width: 320px;
  z-index: 7;
}
.mod-drawer__container .mod-drawer-nav .mod-drawer-nav__list[aria-hidden=false] {
  right: 0;
}
.mod-drawer__container .mod-drawer-nav .mod-drawer-nav__list .navItem a {
  display: inline-block;
  padding: 14px 24px;
  width: 100%;
}
@media screen and (min-width: 992px) {
  .mod-drawer__container {
    display: none;
  }
}

.c-topScroll-btn {
  align-items: center;
  background: #333333;
  bottom: 16px;
  border: none;
  display: flex;
  height: 50px;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  right: 20px;
  transition: all 0.3s ease 0s;
  width: 50px;
  z-index: 4;
}
.c-topScroll-btn .topScroll-Icon {
  border-left: 1px solid #ffffff;
  border-top: 1px solid #ffffff;
  display: block;
  position: relative;
  top: 5px;
  transform: rotate(45deg);
  height: 16px;
  width: 16px;
}
.c-topScroll-btn.is-topScroll {
  bottom: 32px;
  opacity: 1;
  pointer-events: auto;
}

